import React, {Component} from "react";

class ToggleSemester extends Component
{
  render()
  {
    return (
      <div className="tc pa1">
        <input 
          type="button" 
          value="Fall 2018"
          name="Fall 2018"
          className="tc bg-light-gray dib br3 pa1 mh0 mv0 ba b--black"
          onClick={this.props.semesterChange}
        />
        <input 
          type="button" 
          value="Spring 2019"
          name="Spring 2019"
          className="tc bg-light-gray dib br3 pa1 mh0 mv0 ba b--black"
          onClick={this.props.semesterChange}
        />
      </div>
    );
  }
}

export default ToggleSemester;