import React, {Component} from "react";
import CardList from "../components/CardList";
import {coolCats} from "../coolCats";
import SearchBox from "../components/SearchBox";
import Scroll from "../components/Scroll";
import SortOpts from "../components/SortOpts";
import ToggleSemester from "../components/ToggleSemester";
import "./App.css";

class App extends Component
{
  constructor()
  {
    super();
    this.state =
    {
      cats: [],
      searchField: "",
      semester: "",
      sortBy: ""
    }
  }
  
  componentDidMount()
  {
    const chosenCoolCats = coolCats[coolCats.length - 1].users;
    this.setState({cats: chosenCoolCats, 
      semester: coolCats[coolCats.length - 1].term, 
      sortBy: "birthday"});
    // fetch("https://jsonplaceholder.typicode.com/users")
    //   .then(response => response.json())
    //   .then(users => this.setState({cats: users}));
  }
  
  onSearchChange = (event) =>
  {
    this.setState({searchField: event.target.value});
  }

  onSemesterChange = (event) =>
  {
    const newSemester = event.target.name;

    // // Bad idea but potentially useful in future
    // for (var i = 0; i < coolCats.length; i++)
    // {
    //   if (coolCats[i].term === this.state.semester)
    //   {
    //     chosenCoolCats = coolCats[i].users;
    //     break;
    //   }
    // }

    // Currently need to manually add semesters
    if (newSemester === "Fall 2018")
    {
      const chosenCoolCats = coolCats[0].users;
      this.setState({cats: chosenCoolCats, semester: newSemester});
    }
    else if (newSemester === "Spring 2019")
    {
      const chosenCoolCats = coolCats[1].users;
      this.setState({cats: chosenCoolCats, semester: newSemester});
    }
    else
    {
      this.setState({semester: newSemester});
    }
  }

  onSortChange = (event) =>
  {
    this.setState({sortBy: event.target.name});
  }
  
  compareByName = (a, b) => 
  {
    return a.name.localeCompare(b.name);
  }
  
  compareByBirthday = (a, b) => 
  {
    return Date.parse(a.birthday) - Date.parse(b.birthday);
  }
  
  render()
  {
    const {cats, searchField, sortBy} = this.state;
    let filteredCats = this.state.cats.filter(cat => 
    {
      return (
        cat.name.toLowerCase().includes(searchField.toLowerCase()) ||
        cat.birthday.toLowerCase().includes(searchField.toLowerCase())
      );
    });

    if (sortBy === "name")
    {
      filteredCats = filteredCats.sort(this.compareByName);
    }
    else if (sortBy === "birthday")
    {
      filteredCats = filteredCats.sort(this.compareByBirthday);
    }
    
    if (cats.length === 0)
    {
      return <h1 className="tc f1">Loading...</h1>
    }
    else
    {
      return (
        <div>
          <h1 className="tc f2 mb1">ScAlley Cats + Birthdays ({this.state.semester})</h1>
          <SearchBox searchChange={this.onSearchChange}/>
          <ToggleSemester semesterChange={this.onSemesterChange}/>
          <SortOpts sortChange={this.onSortChange}/>
          <Scroll>
            <CardList cats={filteredCats}/>
          </Scroll>
        </div>
      );
    }
  }
}

export default App;