import React, {Component} from "react";
import Card from "./Card";

class CardList extends Component
{
  render()
  {
    const cats = this.props.cats;
    
    return (
      <div>
        {
          cats.map((user, i) => 
          {
            return (
              <Card 
                key={i} 
                name={user.name}
                birthday={user.birthday}
              />
            );
          })
        }
      </div>
    );
  }
}

export default CardList;